:root{
  --bg: #f6f8fb;
  --card: #ffffff;
  --muted: #6c757d;
  --accent: #0d6efd;
  --success: #198754;
}
html,body{height:100%;}
body{
  font-family: 'Inter', system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  background: var(--bg);
  color: #0b1220;
  margin:0;
}
.site-header{background:linear-gradient(90deg,#fff 0%, #fbfdff 100%);}
.card{background:var(--card);}
.card-title{font-weight:600}
.btn-primary{background:var(--accent); border-color:transparent}
.progress{background:rgba(13,110,253,0.08)}
.progress-bar{background:linear-gradient(90deg,var(--accent),#4da3ff)}
#myBarAirmotion.progress-bar{background:linear-gradient(90deg,var(--success),#4ddc7a)}

.terminal{height:320px; overflow:hidden; background:#000;}
.terminal .xterm{height:100%}

video{max-height:220px; object-fit:cover}

/* Small screens tweaks */
@media (max-width:575px){
  .terminal{height:220px}
}

.alert-danger{background:#fff0f0; border-color:#ffd6d6}

/* Small helper */
.small{font-size:.9rem}

/* Spinner overlay shown while connecting */
.spinner-overlay{
  position:fixed;
  inset:0; /* cover entire viewport */
  display:flex;
  align-items:center;
  justify-content:center;
  background: rgba(10,12,16,0.35);
  z-index:1050;
}
.spinner-inner{background:var(--card); padding:18px 22px; border-radius:10px; box-shadow:0 6px 20px rgba(11,18,32,0.12)}
.spinner-text{font-weight:600; color:var(--muted)}
